* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;

  /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#252525+0,424242+100 */
  background: #252525; /* Old browsers */ /* FF3.6-15 */ /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom,  #252525 0%,#424242 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#252525', endColorstr='#424242',GradientType=0 ); /* IE6-9 */
}

/* ====== GENERAL STYLES ====== */
.page {
  max-width: 1200px;
  width: 100vw;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin: 0 auto;
  padding: 0 2%;
  font-family: Abel, sans-serif;
  color: white;
}

section {
  margin: 60px 0;
}

.section-heading {
  text-align: center;
  background: rgba(0,0,0,0.6);
  padding: 25px 0;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-size: 2em;
  margin-bottom: 30px;
}

@media (min-width: 576px) {
  .section-heading {
    font-size: 2.6em;
  }
}

header {
  margin-bottom: 40px;
}

.site-name {
  padding: 20px 10px;
  margin-top: 20px;
  text-align: center;
  color: #D0D0D0;
  text-shadow: 1px 1px 5px rgba(0,0,0,0.6);
  font-family: Condiment, sans-serif;
}

.site-name span {
  color: #E3A820;
}

header nav ul {
  padding: 0;
  list-style: none;
}

header nav li {
  text-align: center;
}

header nav a {
  color:  #E3A820;
  font-size: 1.2em;
  text-decoration: underline;
}

header nav a:hover {
  color: #D0D0D0;
}

@media (min-width: 736px) {
  .site-name {
    text-align: left;
    font-size: 4em;
  }

  header nav li {
    text-align: right;
    margin-right: 60px;
  }
}

.search input {
  width: 98%;
  margin: 0;
  border-radius: 0;
  background: #EDEFF5;
}

.search input:focus {
  outline: none !important;
  border: 1px solid #E3A820;
  box-shadow: 0 0 5px #EAE5D1;
}

main {
  -webkit-flex: 1 1;
          flex: 1 1;
}

/* ========== VIDEO SECTION =============== */
.video {
  margin: 0 auto;
}

/* ======== FEATURED EVENTS SECTION ========= */
/*
  Styles for Bootstrap carousel go here if needed
*/

/* ========== EVENTS SECTION =============== */
.events {
  overflow: hidden;
}

::-webkit-scrollbar {
    width: 0px;  /* remove scrollbar space */
    background: transparent;  /* optional: just make scrollbar invisible */
}

.events .row {
  -webkit-justify-content: space-around;
          justify-content: space-around;
  margin: 0;
}
.event {
  max-width:96%;
  text-align: center;
  background: rgba(0,0,0,0.6);
  margin: 40px 0;
  padding: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  box-shadow: 0px 0px 3px rgba(0,0,0,0.8);
}

.event:hover {
  box-shadow: 0px 0px 5px rgba(190,136,29, 0.4);
}

.event-name {
  padding: 15px 5px;
  text-transform: uppercase;
  font-size: 1.5em;
  margin: 0;
}

.event-img {
  width: 100%;
  height: auto;
  -webkit-flex: 1 1;
          flex: 1 1;
}

.event-info {
  padding: 10px 0 0;
}

.event-info p {
  margin: 0;
}

.event-time-loc {
  color: #E3A820;
  font-size: 1.4em;
}

.event-about {
  padding: 10px;
  text-align: left;
}

.event-about {
  padding: 10px;
  text-align: left;
}

hr {
  background: #E3A820;;
  margin: 0 10px;
}

.event-date {
  font-size: 2.4em;
  margin: 0;
  padding: 20px 0 0;
  font-family: Condiment, sans-serif;
}

.event-signup-btn {
  width: 100%;
  border-radius: 0;
}

.event-signup-btn {
  background: #E3A820;
  color: white;
  font-size: 1.4em;
  font-weight: bold;
}

.event-signup-btn:hover {
  background: #BE881D;
}

/* ==== Pop Up Modal For Event Sign Up ==== */
.modal {
  color: #222;
}

.modal .btn-primary {
  background: #E3A820;
  border: 1px solid #E3A820;
}

.modal .btn-primary:hover {
  background: #BE881D;
}

.modal-footer .btn-secondary {
  border-radius: 0;
}

/* ==== Event Sign Up Form ==== */
.event-signup-form {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.event-signup-form * {
  height: 40px;
}

.event-signup-form input {
  padding-left: 5px;
  margin: 5px 0;
}

.event-signup-form button {
  color: white;
  background: #E3A820;
  border-color: #E3A820;
  font-weight: bold;
  margin-top: 5px;
}

.event-signup-form button:hover {
  background: #BE881D;
}
/* ======== NEWSLETTER SECTION ============= */
.newsletter-contents {
  background: rgba(0,0,0,0.6);
  padding: 20px 20px 40px;
  text-align: center;
  max-width: 400px;
  margin: 0 auto;
}
.newsletter-contents p {
  font-size: 1.3em;
  color: #C1C4CD;
}

/* ==== Newsletter Form ==== */
.newsletter-form {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.newsletter-form input {
  border-radius: 0;
  border: none;
}

.newsletter-form input:focus {
  outline: none !important;
  border: 1px solid #E3A820;
  box-shadow: 0 0 5px #EAE5D1;
}

.newsletter-form button {
  background: #E3A820;
  color: white;
  border: none;
}

.newsletter-form button:hover {
  background: #BE881D;
}

.newsletter-form .msg-alert {
  display: block !important;
}

.newsletter-form .msg-alert p {
  color: #E3A820 !important;
}

footer {
  text-align: center;
  padding: 10px 0;
}

.copyright {
  font-size: 0.6em;
  margin: 0;
  padding-top: 10px;
}

.footer-content ul {
  padding: 0;
}

.footer-content li {
  list-style: none;
  font-size: 0.78em;
}

.footer-content button {
  color: white;
  border: none;
  background: none;
  cursor: pointer;
}

.footer-content button:hover {
  color: #E3A820;
}

footer img {
  max-width: 50px;
  height: auto;
}

